<template>
  <div class="">
    <mt-button @click.native="openConfirm">捕捉</mt-button>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui'
import { mapState } from 'vuex'
export default {
  params: {
    petId: {
      type: String,
      default: '',
      required: true
    }
  },
  data () {
    return {

    }
  },
  methods: {
    openConfirm () {
      let _this = this
      if (!this.petId) {
        return
      }
      else {
        MessageBox.confirm('确定花费5个币捕捉吗？', '重要').then(action => {
          this.$store.dispatch('getCapture', {
            params: {
              petId: _this.petId
            },
            cb: function (data) {
              console.log(data)
            }
          })
        }).catch(action => {
          console.log(action)
        })
      }
    }
  },
  computed: {
    ...mapState({
      english: state => state.english,
      pet: state => state.pet
    })
  }
}
</script>

<style lang="scss" scoped>

</style>



