<script>
import { mapState } from 'vuex'
import Introduction from '../components/pet/introduction'
import TrainRecord from '../components/pet/trainrecord'
import Benefit from '../components/pet/benefit'
import FamilyTree from '../components/pet/familytree'
import Train from '../components/pet/train'
import Sell from '../components/pet/sell'
import Capture from '../components/pet/capture'
export default {
  data () {
    return {
      petDetailTab: 'introduction',
      petBtnTab: 'train',
      petAttribute: [],
      petAttributeText: ['敏捷', '力量', '智力'],
      petAttributeTextEn: ['Agile', 'Force', 'Intelligence']
    }
  },
  filters: {
    toFixed2 (value) {
      return (parseFloat(value)).toFixed(2)
    }
  },
  mounted () {
    this.petAttributes()
  },
  computed: {
    ...mapState({
      english: states => states.english,
      pet: states => states.pet
    })
  },
  methods: {
    backHome () {
      this.$router.replace({name: 'Home'})
    },
    petAttributes () {
      let _this = this
      this.$store.dispatch('getPetAttribute', {
        params: {
          petId: _this.pet.number
        },
        cb: function (data) {
          console.log(data)
          _this.petAttribute = data.attributes
        }
      })
    }
  },
  components: {
    Introduction,
    TrainRecord,
    Benefit,
    FamilyTree,
    Train,
    Sell,
    Capture
  }
}
</script>
<template>
  <div class="pet">
    <mt-header fixed title="pet detail">
      <span slot="left"># {{ pet.number }}</span>
      <mt-button slot="right" class="iconfont icon-shouye" @click.native="backHome"></mt-button>
    </mt-header>
    <div class="pet-detail panel">
      <div class="clearfix">
        <div class="fl">
          <img class="pet-img" :src="pet.imgurl" alt="">
        </div>
        <div class="fr">
          <ul>
            <li v-for="(item, index) in petAttribute" class="pet-attr">
              <span>
                <span v-if="!english">{{ petAttributeText[index] }}</span>
                <span v-else>{{ petAttributeTextEn[index] }}</span>
              </span>
              : {{ item.value | toFixed2 }}
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="pet-tabs panel">
      <mt-navbar v-model="petDetailTab">
        <mt-tab-item id="introduction"><span><span v-if="!english">简介</span><span v-else>introduction</span></span></mt-tab-item>
        <mt-tab-item id="trainrecord"><span><span v-if="!english">训练记录</span><span v-else>train record</span></span></mt-tab-item>
        <mt-tab-item id="benefit"><span><span v-if="!english">收益记录</span><span v-else>benefit</span></span></mt-tab-item>
        <mt-tab-item id="familytree"><span><span v-if="!english">族谱</span><span v-else>family tree</span></span></mt-tab-item>
      </mt-navbar>

      <!-- tab-container -->
      <mt-tab-container class="tab-items" v-model="petDetailTab">
        <mt-tab-container-item id="introduction">
          <introduction></introduction>
        </mt-tab-container-item>
        <mt-tab-container-item id="trainrecord">
          <train-record></train-record>
        </mt-tab-container-item>
        <mt-tab-container-item id="benefit">
          <benefit></benefit>
        </mt-tab-container-item>
        <mt-tab-container-item id="familytree">
          <family-tree></family-tree>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
    <div class="pet-tabs panel">
      <mt-navbar v-model="petBtnTab">
        <mt-tab-item id="train"><span><span v-if="!english">训练</span><span v-else>train</span></span></mt-tab-item>
        <mt-tab-item id="sell"><span><span v-if="!english">出售</span><span v-else>sell</span></span></mt-tab-item>
        <mt-tab-item id="capture"><span><span v-if="!english">捕捉</span><span v-else>capture</span></span></mt-tab-item>
      </mt-navbar>

      <!-- tab-container -->
      <mt-tab-container class="tab-items" v-model="petBtnTab">
        <mt-tab-container-item id="train">
          <train></train>
        </mt-tab-container-item>
        <mt-tab-container-item id="sell">
          <sell></sell>
        </mt-tab-container-item>
        <mt-tab-container-item id="capture">
          <capture></capture>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.pet {
  .pet-detail {
    margin-top: 50px;
    .pet-img {
      display: inline-block;
      height: 120px;
      width: 120px;
      background-color: #cdf5d4;
    }
    .pet-attr {
      line-height: 3;
    }
  }
  .tab-items {
    margin: 20px 0;
  }
}
</style>


